@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.isCompleted {
  color: grey;
  text-decoration: line-through;
}

.isNotCompleted {
  color: #333;
}
.AddTodo{display:flex;justify-content:space-between;width:100%}.AddTodo input,.AddTodo button{height:3rem;border-radius:.2rem}.AddTodo input{width:80%;margin-right:1rem;padding:1rem;border:1px solid rgba(0,0,0,.3);outline:none;transition:background-color .3s}.AddTodo input:focus{background-color:#eee}.AddTodo button{width:100px;background-color:#6b8e23;border-color:transparent;color:#fff}.AddTodo button:hover{cursor:pointer}
.TodoList{width:100%;background-color:#eee;padding:2rem}
.Todo{display:flex;align-items:center;margin-bottom:1rem;padding:.5rem;border-bottom:1px solid rgba(0,0,0,.2)}.Todo input{margin-right:.4rem}.Todo button{margin-left:auto;background-color:crimson;padding:.5rem;color:#fff}
*{box-sizing:border-box}body{font-family:"Roboto",sans-serif;font-size:16px}ul{margin:0;padding:0;list-style-type:none}button{border:none;outline:none;text-transform:uppercase}button:hover{cursor:pointer}.App{display:flex;flex-direction:column;align-items:center}header{padding:2rem 0;width:40%;text-align:center}main{width:40%}section{margin:3rem 0}
